import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ForgetPasswordPage } from '../forget-password/forget-password';
import { Storage } from '@ionic/storage';
import { HomePage } from '../home/home';
import { Md5 } from 'ts-md5';
import { HttpClient } from "@angular/common/http";
import { RestProvider } from "../../providers/rest/rest";

/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

interface credentials {
  code: any;
}

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  public isRemember:boolean = false;
  public isShow:boolean = false;
  path: string = 'http://88.128.18.227:8080/cgb/rest/';
  token: string;
  access: boolean;
  code: any;
  user: any;
  data: any;


  constructor(public navCtrl: NavController, public navParams: NavParams, public storage: Storage, private http: HttpClient,
              private restProvider: RestProvider) {

  }


  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
  }





  public rememberPassword(username:HTMLInputElement,password:HTMLInputElement) {
    this.isRemember=true;
    window.localStorage.setItem('isRemember',JSON.stringify(true));
    let pwd = password.toString();
    let hashPwd = Md5.hashStr(pwd);
    this.navCtrl.setRoot(HomePage);
  }


  logIn(username: HTMLInputElement, password: HTMLInputElement) {
    if (username.value.length == 0) {
      alert("请输入账户名");
      return;
    } else if (password.value.length == 0) {
      alert("请输入密码");
      return;
    }
    if(this.isRemember == true){
      let pwd = password.toString();
      let hashPwd = Md5.hashStr(pwd);
      let data = {username: username.value, password: hashPwd, isRemember: this.isRemember};
      this.storage.remove("USER_INFO");
      this.storage.set("USER_INFO", JSON.stringify(data));
      this.navCtrl.setRoot(HomePage);
      return;
    }
    let pwd = password.value.toString();
    let hashPwd = Md5.hashStr(pwd);
    let name = JSON.stringify(username.value);
    this.restProvider.login(name, hashPwd);
    this.navCtrl.push(HomePage);
    }


  goForgetPwd(){
    this.navCtrl.push(ForgetPasswordPage);
  }

};




